export default "/* generic */\nbody {\n  background-color: #eee;\n  color: #444;\n  font-family: sans-serif; }\n.list ul:nth-child(odd) {\n  background-color: #ddd; }\n.list ul:nth-child(even) {\n  background-color: #fff; }\n/* big */\n@media screen and (min-width: 600px) {\n  .list {\n    display: table;\n    margin: 1em; }\n  .list ul {\n    display: table-row; }\n  .list ul:first-child li {\n    background-color: #444;\n    color: #fff; }\n  .list ul > li {\n    display: table-cell;\n    padding: .5em 1em; } }\n/* small */\n@media screen and (max-width: 599px) {\n  .list ul {\n    border: solid 1px #ccc;\n    display: block;\n    list-style: none;\n    margin: 1em;\n    padding: .5em 1em; }\n  .list ul:first-child {\n    display: none; }\n  .list ul > li {\n    display: block;\n    padding: .25em 0; }\n  .list ul:nth-child(odd) > li + li {\n    border-top: solid 1px #ccc; }\n  .list ul:nth-child(even) > li + li {\n    border-top: solid 1px #eee; }\n  .list ul > li:before {\n    color: white;\n    content: attr(data-label);\n    display: inline-block;\n    font-size: 75%;\n    font-weight: bold;\n    text-transform: capitalize;\n    vertical-align: top;\n    width: 50%; }\n  .list p {\n    margin: -1em 0 0 50%; } }\n/* tiny */\n@media screen and (max-width: 349px) {\n  .list ul > li:before {\n    display: block; }\n  .list p {\n    margin: 0; } }\n.positive {\n  color: #ccae03 !important;\n  font-weight: bolder;\n  border: 2px solid #ccae03; }\n.negative {\n  color: #091aac !important;\n  font-weight: bolder;\n  border: 2px solid #091aac; }\n.full {\n  color: #f30909 !important;\n  font-weight: bolder;\n  border: 2px solid #f30909; }\n.empty {\n  color: #21c221 !important;\n  font-weight: bolder;\n  border: 2px solid #07f707; }\n\n/*# sourceMappingURL=data:application/json;base64, */"